<template>
    <div>
      <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"

      @load="onLoad"
      :immediate-check="false"
    >
     <van-cell v-for="data in datalist" :key="data.id"  @click="handleClick(data.filmId)"><img :src="data.poster" alt="">{{data.name}}</van-cell>
   </van-list>
    </div>
</template>
<script>
import http from '../../util/http'
import Vue from 'vue'
import { List, Cell } from 'vant'
import { mapState } from 'vuex'

Vue.use(List).use(Cell)
export default {
  data () {
    return {
      datalist: [],
      loading: false,
      finished: false,
      current: 1,
      total: 0

    }
  },
  mounted () {
    http({
      url: `/gateway?cityId=${this.cityId}&pageNum=1&pageSize=10&type=1&k=2996029`,
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1610458637722885945589761","bc":"440300"}',
        'X-Host': 'mall.film-ticket.film.list'
      }

    }).then(res => {
      this.datalist = res.data.data.films
      this.total = res.data.data.total
    })
  },
  computed: {

    ...mapState('CityModule', ['cityId'])
  },
  methods: {

    handleClick (id) {
      // 1.通过js编程式导航
      // this.$router.push(`/detail/${id}`)  //通过js编程式导航
      // 2.路由名字
      // this.$router.push(
      //     {
      //         name:'detail',
      //         params:{
      //             myid:id
      //         }
      //     }
      // )
      // query传参数方式跳转详情
      this.$router.push(`/detail?id=${id}`)
    },
    onLoad () {
      if (this.datalist.length === this.total && this.datalist.length !== 0) {
        this.finished = true
        return
      }
      console.log('到底了')
      this.current++
      http({
        url: `/gateway?cityId=${this.cityId}&pageNum=${this.current}&pageSize=10&type=1&k=2996029`,
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1610458637722885945589761","bc":"440300"}',
          'X-Host': 'mall.film-ticket.film.list'
        }

      }).then(res => {
        console.log(res.data)
        this.datalist = [...this.datalist, ...res.data.data.films]
        this.loading = false
      })
    }

  }
}
</script>
<style lang="scss">
van-list{
  van-cell{
      list-style: none;
    img{
      // float: left;
      width: 200px;

    }
  }
}

</style>
