<template>
    <div>
       <van-index-bar :index-list="computedcityList" @select="handleSelect()">
           <div v-for="data in cityList" :key="data.type">
        <van-index-anchor :index="data.type" />
        <van-cell :title="item.name" v-for="(item,index) in data.list" :key="index" @click="handleChangePage(item.name,item.cityId)" />
        </div>
        </van-index-bar>
    </div>
</template>

<script>
import Vue from 'vue'
import { IndexBar, IndexAnchor, Cell, Toast } from 'vant'
import http from '../util/http'
import { mapMutations } from 'vuex'
Vue.use(IndexBar).use(Cell)
Vue.use(IndexAnchor)

export default {
  data () {
    return {
      cityList: []
    }
  },
  computed: {
    computedcityList () {
      return this.cityList.map(item => item.type)
    }
  },
  mounted () {
    http({
      url: 'https://m.maizuo.com/gateway?k=274525',
      headers: {
        'X-Host': 'mall.film-ticket.city.list'
      }
    }).then(res => {
      this.cityList = this.handleCityData(res.data.data.cities)
    })
  },
  methods: {
    ...mapMutations('CityModule', ['changeCityName', 'changeCityId']),
    handleCityData (cities) {
      const letterArr = []
      const newcities = []

      for (let code = 65; code < 91; code++) {
        letterArr.push(String.fromCharCode(code))
      }
      letterArr.forEach((letter) => {
        const list = cities.filter(item =>
          item.pinyin.substring(0, 1).toUpperCase() === letter
        )
        if (list.length > 0) {
          newcities.push({
            type: letter,
            list: list
          })
        }
      })
      return newcities
    },
    handleSelect (index) {
      Toast(index)
    },
    handleChangePage (name, id) {
      this.changeCityName(name)
      this.changeCityId(id)
    }

  }
}
</script>
