<template>
    <div>
        <van-nav-bar
            title="标题"
            @click-left="onClickLeft()"
            @click-right="onClickRight()"
            >
            <!-- v-slot用#简写 -->
             <template #left>
                {{$store.state.cityName}}<van-icon name="arrow-down" />
            </template>

             <template #right >
                <van-icon name="search" size="18" />
            </template>
        </van-nav-bar>

            <div class="cinema" :style="{height:height}">
                <van-list>
                    <van-cell v-for="(item,index) in this.cinemaList" :key="index" >
                        <div style="margin-top:5px;">{{item.name}}</div>
                        <div style="font-size:12px;color:grey; margin-top:10px">{{item.address}}</div>
                   </van-cell>
                </van-list>
            </div>
    </div>
</template>

<script>
import BetterScroll from 'better-scroll'
import Vue from 'vue'
import { NavBar, Icon, Search } from 'vant'
import { mapActions, mapMutations, mapState } from 'vuex'

Vue.use(Search)

Vue.use(NavBar).use(Icon)

export default {
  data () {
    return {
      height: 0
    }
  },
  computed: {
    ...mapState('CinemaModule', ['cinemaList']),
    ...mapState('CityModule', ['cityId', 'cityName'])
  },
  methods: {
    ...mapActions('CinemaModule', ['getCinemaList']),
    ...mapMutations('CinemaModule', ['clearCinemaList']),

    onClickLeft () {
      // 清空cinemaList
      this.clearCinemaList()

      this.$router.push('/city')
    },
    onClickRight () {
      // 清空cinemaList
      this.clearCinemaList()

      this.$router.push('/cinema/search')
    }
  },
  mounted () {
    this.height = document.documentElement.clientHeight - 100 + 'px'

    if (this.cinemaList.length === 0) {
      // vuex异步流程
      this.getCinemaList(this.cityId)
        .then(
          this.$nextTick(() => {
            new BetterScroll('.cinema', {
              scrollbar: {
                fade: true
              }
            })
          })
        )
    } else {

    }
  }

}
</script>

<style lang="scss" scoped>
    li{
        height:60px;
        border-bottom:1px solid grey;
        margin-left:10px;
        margin-right:10px;
    }
    .cinema{

        // overflow: hidden;
        position: relative;
    }
</style>
