import http from '../../util/http'
const module = {
  namespaced: true,
  state: {
    isTabbarShow: true,
    cinemaList: []
  },
  // 变异 突变 转变
  mutations: {

    setCinemaList (state, cinemaList) {
      state.cinemaList = cinemaList
    },
    clearCinemaList (state, cinemaList) {
      state.cinemaList = []
    }

  },
  actions: {
    getCinemaList (store, cityId) {
      // promise对象
      return http({
        url: `https://m.maizuo.com/gateway?cityId=${cityId}&ticketFlag=1&k=850561`,
        headers: {
          'X-Host': 'mall.film-ticket.cinema.list'
        }
      }).then(res => {
        console.log(res.data)
        store.commit(
          'setCinemaList', res.data.data.cinemas
        )
      })
    }

  }

}
export default module
