<template>
    <div>
     <div class="back" @click="onClickLeft()">
        <i></i>
     </div>
        <div v-if="filminfo">
          <div :style="{backgroundImage:'url('+filminfo.poster+')'}" style="height:250px;background-size:cover"></div>
          <div>{{filminfo.name}}</div>
          <div>{{filminfo.item.name}}</div>
          <div>{{filminfo.category}}</div>
          <div>{{filminfo.premiereAt | dateFormat}}</div>
          <div>{{filminfo.nation}}|{{filminfo.runtime}}分钟</div>

          <div :class="isShow?'':'synopsis'" >{{filminfo.synopsis}}</div>
          <div style="text-align:center;" class="trangle-box" @click="isShow=!isShow">
            <div  :class="isShow?'trangles':'trangle'"></div>
          </div>
          <div class="box2">
          <h3>演职人员</h3>
          <detail-swiper :perslide="4" swiperclass="swiper-actors">
            <div class="swiper-slide" v-for="(data,index) in filminfo.actors" :key="index">
              <img :src="data.avatarAddress" alt=""/>
              <div style="text-align:center">
                <div>{{data.name}}</div>
                <div>{{data.role}}</div>
              </div>
            </div>
          </detail-swiper>
          <h3>剧照</h3>
          <detail-swiper :perslide="2" swiperclass="swiper-photos">
            <div class="swiper-slide" v-for="(data,index) in filminfo.photos" :key="index">
              <img  class="img" :src="data" alt="" @click="handlePreview(index)" />
            </div>
          </detail-swiper>
        </div>
        </div>
    </div>
</template>

<script>
import http from '../util/http'
import moment from 'moment'
// import fanhui from '../components/fanhui'
import Vue from 'vue'
import detailSwiper from './detail/DetailSwiper'

import { ImagePreview } from 'vant'
import { mapMutations } from 'vuex'

Vue.filter('dateFormat', (date) => {
  // return moment(date*1000).format('MMMM Do YYYY, H:mm:ss a')
  return moment(date * 1000).format('YYYY-MM-DD')
})
export default {
  data () {
    return {
      isShow: false,
      filminfo: null
    }
  },
  components: {
    detailSwiper
    // fanhui
  },

  mounted () {
    // 动态路由
    // console.log(this.$route.params.myid);
    // query方式，编程式
    // console.log(this.$route.query.id)
    this.hide()

    http({
      url: `gateway?filmId=${this.$route.query.id}&k=4753970`,
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16108824201227398573981697","bc":"440300"}',
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then(res => {
      console.log(res.data)
      this.filminfo = res.data.data.film
    })
  },
  beforeDestroy () {
    this.show()
  },

  methods: {
    ...mapMutations('TabbarModule', ['show', 'hide']),
    handlePreview (index) {
      ImagePreview({
        images: this.filminfo.photos,
        startPosition: index,
        closeable: true,
        closeIconPosition: 'top-left'
      })
    },
    onClickLeft () {
      this.$router.back()
    }
  }
}
</script>
