<template>
    <div>
        <van-search
            v-model="value"
            show-action
            placeholder="请输入搜索关键词"
             @cancel="handleCancel"

  /> <van-list>
                    <van-cell v-for="item in computedCinemaList" :key="item.cinemaId" >
                        <div style="margin-top:5px;">{{item.name}}</div>
                        <div style="font-size:12px;color:grey; margin-top:10px">{{item.address}}</div>
                    </van-cell>
                </van-list>
    </div>
</template>

<script>
import Vue from 'vue'
import { Search } from 'vant'
Vue.use(Search)

export default {
  data () {
    return {
      value: ''

    }
  },
  computed: {
    computedCinemaList () {
      if (this.value === '') { return [] }
      return this.$store.state.cinemaList.filter(item => item.name.toUpperCase()
        .includes(this.value.toUpperCase()) || item.address.toUpperCase().includes(this.value.toUpperCase()))
    }
  },
  mounted () {
    if (this.$store.state.cinemaList.length === 0) {
      // vuex异步流程
      this.$store.dispatch('getCinemaList', this.$store.state.cityId)
    } else {

    }
  },
  methods: {
    handleCancel () {
      // push 列表跳详情 back,replace
      this.$router.replace('/cinema')
    }
  }
}
</script>
